.modal {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    &-block {
        padding: 10px;
        width: 350px;
        height: 400px;
        background: #fff; }
    &__header {
        display: flex;
        // align-content: center
        align-items: center;
        border-bottom: 1px solid #333;
        padding-bottom: 10px;
        div {
            margin-left: 15px; }
        div:first-child {
            margin-left: 0; }
        button {
            margin-left: auto;
            padding: 2px 5px; } }
    &__content {
        padding-top: 15px;
        display: flex;
        align-items: center;
        align-content: center;
        justify-content: center;
        flex-direction: column;
        h2 {
            text-align: center; } } }


.form {
    padding-top: 55px;
    flex-direction: column;
    display: flex;
    align-items: center;
    &__submit {
        max-width: 80%;
        margin-top: 30px; } }

.hide {
    display: none; }
